.grid {
	width: 1000px;
	margin: 15px auto;
	margin-left:50px;
	display: grid;
	grid-template-rows: 250px 250px 250px;
	grid-template-columns: 430px 900px;
	
    grid-row-gap: 5x;
    
}
@media screen and (max-width: 531px) {
	.grid { 
	  display: flex; 
	  flex-flow: column; 
	  text-align: center; 
	  width:100%;

	}
  }
